.home-end{
    width:100%;
    height:10%;
    background-color: black;
    color: #f08804;
    display:flex;
    padding-bottom: 5%;
    font-weight: 600;
}
.home-end-about-us{
    width: 40%;
    height: 100%;
    display: block;
    margin-top: 1%;
    margin-left: 10%;
    font-size: 100%;
}

.home-end-contact-info{
    width: 40%;
    height: 100%;
    display: block;
    margin-top: 1%;
    margin-left: 5%;
}

.home-end-contact-info ul li{
    display: block;
    list-style-type: none;
    margin-top: 2%;
    margin-bottom: 2%;
}

.home-end-contact-info ul li i{
    margin-right: 1%;
}

.home-end-contact-info ul li a{
    color: #f08804;
    display: inline-block;
    width:40%;
    font-size: 100%;
    margin-left: 1%;
    text-decoration: none;
}

.home-end-contact-info button{
    display: inline-block;
    background-color: black;
    color: #f08804;
    border-color: white;
    border-radius: 20%;
    padding: 1%;
    margin-top: 1%;
    margin-left: 0;
    font-size: 100%;
}

.footer{
    width:100%;
    height:10vh;
    background-color: black;
    padding-top: 1%;
    padding-bottom: 1%;
}

.footer-heading{
    color:#fff;
    width: 40%;
    height: 100%;
    display: flex;
    align-items: center;
    margin-left: 10%;
    font-size: 100%;
}
